*{padding:0;margin:0;box-sizing:border-box;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body{margin:0;font-family:"Open Sans",sans-serif,-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;color:#222;background-color:#fff}

.restaurant__card{display:grid;grid-template-rows:1fr 1fr;background-color:#fff;box-shadow:0 10px 15px -3px rgba(0,0,0,0.1),0 4px 6px -2px rgba(0,0,0,0.05)}.restaurant__card .restaurant__card__image--container{grid-row:span 1;width:100%;padding-top:56.25%;overflow:hidden;position:relative;pointer-events:none;border-top-left-radius:8px;border-top-right-radius:8px}.restaurant__card .restaurant__card__image--container img{pointer-events:none;width:100%;height:auto;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.restaurant__card .restaurant__card__content{grid-row:span 1;padding:2rem;pointer-events:none;border-bottom-left-radius:8px;border-bottom-right-radius:8px;display:flex;flex-direction:column;justify-content:space-between}.restaurant__card .restaurant__card__content .card__title{font-family:"Varela Round",sans-serif;text-transform:uppercase;font-size:1.8rem;font-weight:500;line-height:normal}.restaurant__card .restaurant__card__content .card__price{font-size:1.6rem;font-family:"Varela Round",sans-serif;color:#de923a;margin-bottom:1rem}.restaurant__card .restaurant__card__content .card__info{flex-grow:1}.restaurant__card .restaurant__card__content .card__description{font-family:"Open Sans",sans-serif;font-weight:300;font-size:1.4rem}.restaurant__card .restaurant__card__content .card__tags{text-align:right}.restaurant__card .restaurant__card__content .card__tags .card__tag{display:inline-block;text-transform:capitalize;padding:5px 20px;background-color:#c0c0b3;border-radius:2rem}@media only screen and (max-width: 1000px){.restaurant__card{margin:0}.restaurant__card .restaurant__card__image--container img{height:100%}}@media only screen and (max-width: 600px){.restaurant__card{margin:0}.restaurant__card .restaurant__card__image--container img{height:auto}.restaurant__card .restaurant__card__content .card__title,.restaurant__card .restaurant__card__content .card__price{font-size:1.6rem}.restaurant__card .restaurant__card__content .card__description{font-size:1.2rem}}

.restaurant__swiper{width:100%;height:100%;overflow:hidden;display:flex;flex-direction:column;position:relative;opacity:0;-webkit-transition:opacity 0.1s ease-in-out;transition:opacity 0.1s ease-in-out}.restaurant__swiper.loaded{opacity:1}.restaurant__swiper .restaurant__cards{flex-grow:1;display:flex;justify-content:center;align-items:center;z-index:1}.restaurant__swiper .restaurant__cards .restaurant__card{width:90vw;max-width:600px;height:auto;padding-bottom:40px;overflow:hidden;position:absolute;will-change:transform;-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out;cursor:-webkit-grab;cursor:grab}.restaurant__swiper .restaurant__cards .restaurant__card.moving{-webkit-transition:none;transition:none;cursor:-webkit-grabbing;cursor:grabbing}.restaurant__swiper .restaurant__swiper__buttons{z-index:2;flex:0 0 100px;text-align:center}.restaurant__swiper .restaurant__swiper__buttons button{border-radius:50%;line-height:60px;width:60px;border:0;background:#fff;display:inline-block;margin:0 8px;cursor:pointer;box-shadow:0 10px 15px -3px rgba(0,0,0,0.1),0 4px 6px -2px rgba(0,0,0,0.05);-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out}.restaurant__swiper .restaurant__swiper__buttons button:hover,.restaurant__swiper .restaurant__swiper__buttons button:focus{-webkit-transform:scale(1.1);transform:scale(1.1)}.restaurant__swiper .restaurant__swiper__buttons button i{font-size:32px;vertical-align:middle;-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out}.restaurant__swiper .restaurant__swiper__buttons button i:hover,.restaurant__swiper .restaurant__swiper__buttons button i:focus{-webkit-transform:scale(1.1);transform:scale(1.1)}.restaurant__swiper .restaurant__swiper__buttons button .fa-heart{color:#fface4}.restaurant__swiper .restaurant__swiper__buttons button .fa-heart:hover,.restaurant__swiper .restaurant__swiper__buttons button .fa-heart:focus{color:#ff46c3}.restaurant__swiper .restaurant__swiper__buttons button .fa-remove{color:#acd4fd}.restaurant__swiper .restaurant__swiper__buttons button .fa-remove:hover,.restaurant__swiper .restaurant__swiper__buttons button .fa-remove:focus{color:#48a0fb}.restaurant__swiper .restaurant__swiper__buttons button .fa-th-large{color:#e5aa66}.restaurant__swiper .restaurant__swiper__buttons button .fa-th-large:hover,.restaurant__swiper .restaurant__swiper__buttons button .fa-th-large:focus{color:#c47821}.restaurant__swiper .restaurant__swiper__buttons button:focus{outline:0}

.restaurant__list{margin:2rem}.restaurant__list .restaurant__cards{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:2rem;justify-items:center}.restaurant__list .restaurant__cards .restaurant__card{width:100%;grid-column:span 1}.restaurant__list .restaurant__cards .restaurant__card .restaurant__card__content .card__title,.restaurant__list .restaurant__cards .restaurant__card .restaurant__card__content .card__price{font-size:1.4rem}.restaurant__list .restaurant__cards .restaurant__card .restaurant__card__content .card__description{font-size:1.2rem}.restaurant__list .restaurant__swiper__button{position:fixed;top:0;right:0}.restaurant__list .restaurant__swiper__button button{border-radius:50%;line-height:60px;width:60px;border:0;background:#fff;display:inline-block;margin:1rem;cursor:pointer;box-shadow:0 10px 15px -3px rgba(0,0,0,0.1),0 4px 6px -2px rgba(0,0,0,0.05);-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out}.restaurant__list .restaurant__swiper__button button:hover,.restaurant__list .restaurant__swiper__button button:focus{-webkit-transform:scale(1.1);transform:scale(1.1)}.restaurant__list .restaurant__swiper__button button i{font-size:32px;vertical-align:middle;-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out}.restaurant__list .restaurant__swiper__button button i:hover,.restaurant__list .restaurant__swiper__button button i:focus{-webkit-transform:scale(1.1);transform:scale(1.1)}.restaurant__list .restaurant__swiper__button button .fa-th-large{color:#e5aa66}.restaurant__list .restaurant__swiper__button button .fa-th-large:hover,.restaurant__list .restaurant__swiper__button button .fa-th-large:focus{color:#c47821}.restaurant__list .restaurant__swiper__button button:focus{outline:0}@media only screen and (max-width: 1000px){.restaurant__list{margin:1rem}.restaurant__list .restaurant__cards{grid-template-columns:1fr 1fr;grid-gap:10px}}@media only screen and (max-width: 600px){.restaurant__list{margin:0}.restaurant__list .restaurant__cards{grid-template-columns:1fr;grid-gap:5px}}

.App{display:grid;grid-template-rows:auto 1fr;min-height:100vh}.App header{text-align:center}.App header h1{font-family:"Varela Round",sans-serif;font-size:3rem;text-transform:uppercase}.App header .restaurant__search{display:inline-block;width:auto;font-family:"Varela Round",sans-serif;font-size:1.4rem;border-radius:2rem;background-color:#f7f6fb;color:#c0c0b3;padding:1rem}.App header .restaurant__search input{font-family:inherit;font-size:1.4rem;font-weight:400;border:none;background-color:transparent}.App footer{z-index:3;position:absolute;width:100%;bottom:20px;text-align:right}.App footer a{margin:1rem;font-family:"Varela Round",sans-serif;text-decoration:none;color:#c47821}.App footer a span{margin:0.2rem}@media only screen and (max-width: 600px){.App header h1{font-size:2rem}}

